import github from '../media/github.svg'
import linkedin from '../media/linkedin.svg'
import devpost from '../media/devpost.svg'
import "../Balls.js";

function LandingPage() {
    return (
        <div>
            <h1 class="title" id="text">LAWRENCE MAO</h1>
            <h3 class="description" id="text">Developer, Musician, Adventurer</h3>
            <div class="logoContainer">
                <a href="https://github.com/DenouementX" target="_blank">
                    <img class="logo" src={github} alt="GitHub Logo" />
                </a>
                <a href="https://linkedin.com/in/lmao" target="_blank">
                    <img class="logo" src={linkedin} alt="LinkedIn Logo" />
                </a>
                <a href="https://devpost.com/denouementx" target="_blank">
                    <img class="logo" src={devpost} alt="Devpost Logo" />
                </a>
            </div>
        </div>
    )
}
  
  export default LandingPage;