import * as React from 'react';
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import { Link } from "react-router-dom";

export default function SimpleAppBar() {
  return (
    <AppBar position="static" style={{ background: '#E27D60' }}>
      <Toolbar>
        <Typography sx={{ flexGrow: 1 }}>
          <Link to="/" style={{textDecoration: 'none'}}><Button color="inherit" style={{fontFamily: 'FuturaBold', color: 'white'}}>Home</Button></Link>
          <Button color="inherit" style={{fontFamily: 'FuturaBold', color: 'white'}}>Projects</Button>
          <Link to="/aboutme" style={{textDecoration: 'none'}}><Button color="inherit" style={{fontFamily: 'FuturaBold', color: 'white'}}>About Me</Button></Link>
        </Typography>
      </Toolbar>
    </AppBar>
  );
}